.reviews__wrap {
  padding: 20px;
}

.reviews__ttl {
  @include ttl;
}

.reviews__items {
  @include nl;
  margin-top: 30px;
}

.reviews__item {
  @include flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  margin: auto;
}

.reviews__img-block {
  width: 280px;  
}

.reviews__name {
  display: block;
  font-size: 16px;
  text-align: center;
  margin-top: 20px;
}

.reviews__txt {
  width: 280px;
  font-size: 16px;
  font-family: $font-1-l;
  text-align: center;
}

.reviews__indicators {
  @include dots;
}

.reviews__indicator {
  @include dot;
}


@media #{$tablet} {
  .reviews__wrap {
    max-width: $max-tablet;
    margin: auto;
    margin-top: 60px;
  }
  
  .reviews__ttl {
    font-size: $h1-d;
  }
  
  .reviews__items {
    @include flex-row;
    flex-wrap: wrap;
    margin-top: 65px;
  }
  
  .reviews__item {
    margin-top: 20px;    
  }
}


@media #{$desktop} {
  .reviews__wrap {
    max-width: $max-desktop;
  }
  
  .reviews__items {
    justify-content: space-between;
  }
  
  .reviews__item {
    border: 1px solid #f2f2f2;
    width: 368px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin: 0;
    margin-bottom: 30px;
  }
  
  .reviews__img-block {
    width: 296px;
  }
  
  .reviews__txt {
    width: 296px;
  }
}