.require {
  background: url(%=static=%/content/req-girl.png)no-repeat 100% 100%, url(%=static=%/content/req-bg-mobile.jpg)no-repeat 50% 50%;
  margin-top: 20px;  
}
.require__wrap {
  padding: 20px;
  background: rgba(0,0,0, 0.5);
}

.require__ttl {
  @include ttl;
  color: #fff;
}

.require__logo {
  display: block;
  margin: auto;
  width: 280px;
  margin-top: 20px;
}

.require__items {
  @include nl;
  margin-top: 20px;  
}

.require__item {
  margin-bottom: 20px;
}

.require__item-ttl {
  font-family: $font-2;
  font-size: 20px;
  color: #fff;
  &:before {
    content: '';
    display: inline-block;
    @include bg($check-logo);
    margin-right: 10px;
  }
}

.require__txt {
  max-width: 570px;
  font-family: $font-2-l-italic;
  color: #fff;
  font-size: 17px;
  line-height: 30px;
  margin-top: 5px;
}


@media #{$tablet} {
  .require {
    background: url(%=static=%/content/req-bg.jpg)no-repeat 50%;     
    margin-top: 0;
  }
  
  .require__wrap {
    max-width: $max-tablet;
    margin: auto;
    padding-top: 30px; 
    background: none;
  }
  
  .require__ttl {
    font-size: $h1-d;
  }
  
  .require__logo {
    width: auto;
  }
  
  .require__items {
    width: 360px;
  }
}


@media #{$desktop} {
  .require {
    background-position: 50% 0%;
  }
  .require__wrap {
    max-width: $max-desktop;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 57px;
  }
  
  .require__ttl {
    padding-top: 70px;
  }
  
  .require__items {
    width: 50%;
    margin-top: 47px;
  }
}