.footer {
  background: #0b0b0b;
}

.footer__wrap {
  padding: 20px;
}

.footer__logo-block {
  @include flex-row;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.footer__logo {
  @include bg($spa-logo);
  margin-right: 5px;
}

.footer__logo-txt {
  font-family: $font-1-bk-italic;
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
}

.footer__contacts {
  @include flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.footer__phone {
  font-family: $font-2-b;
  color: #fff;
  font-size: 22px;
  &:before {
    content: '';
    display: inline-block;
    @include bg($phone-logo);
    margin-right: 10px;
  }
}

.footer__txt {
  font-family: $font-1-l;
  font-size: 18px;
  color: #fff;
  margin-top: 5px;
}


@media #{$tablet} {
  .footer__wrap {
    max-width: $max-tablet;
    margin: auto;
    @include flex-row;
    justify-content: space-between;
    align-items: center;
  }
  
  .footer__contacts {
    margin: 0;
  }
}

@media #{$desktop} {
  .footer__wrap {
    max-width: $max-desktop;
    padding-top: 40px;
  }
}
