.life { 
  
}

.life__wrap {
  
}

.life__girl {
  background: url(%=static=%/content/life-girl.png)no-repeat 50% 50%;
  background-size: cover;
  padding-top: 45px;
  padding-bottom: 90px;
  position: relative;
}

.life__ttl {
  @include ttl;
  color: #fff;
}

.life__txt {
  font-family: $font-2-l;
  font-size: 17px;
  color: #fff;
  text-align: center;
  margin-top: 10px;
}

.life__logo {
  display: block;
  margin: auto;
  margin-top: 20px;
  width: 280px;
}

.life__info {
  background: #000; 
  margin-top: -10px;  
  padding-top: 30px;
  padding-bottom: 20px;
}

.life__items {
  @include nl;
  @include flex;
  justify-content: center;
  align-items: center;  
}

.life__item {
  font-family: $font-2-italic;
  color: #fff;
  font-size: 16px;
  width: 280px;
  margin-bottom: 10px;
  &:before {
    content: '';
    display: inline-block;
    @include bg($check-logo);
    margin-right: 10px;
  }
}

.life__open-form {
  @include btn;
  margin-top: 30px;
}


@media #{$tablet} {
  .life {
    background: url(%=static=%/content/life-bg.jpg)no-repeat 50% 0%;    
  }
  
  .life__wrap {
    max-width: $max-tablet;
    margin: auto;
    padding-top: 55px;
    padding-bottom: 110px;
  }
  
  .life__girl {
    background: none;
  }
  
  .life__ttl {
    font-size: $h1-d;
  }
  
  .life__txt {
    font-size: 20px;
  }
  
  .life__logo {
    width: auto;
  }
  
  .life__info {
    background: none;
  }
  
  .life__items {
    margin-left: auto;
    margin-right: 0;
    width: 50%;
  }
  
  .life__item {
    width: auto;
    font-size: 20px;
    margin-top: 10px;
    width: 330px;
  }  

  .life__open-form {
    margin-left: auto;
    margin-right: 0;
    font-size: 16px;
    width: 300px;
    margin-right: 30px;
    margin-top: 60px;
  }
}


@media #{$desktop} {  
  .life__wrap {
    max-width: $max-desktop;
    padding-bottom: 120px;
  }
  
  .life__txt {
    margin-top: 25px;
  }
  
  .life__girl {
    padding-top: 20px;
  }
  
  .life__logo {
    margin-top: 30px;
  }
  
  .life__items {
    margin-top: 45px;
    width: 50%;
    align-items: flex-start;
  }  
   
  .life__info {
    padding-top: 0;
  }
  
  .life__item {
    @include flex-row;
    align-items: center;
    margin: 0;
    margin-bottom: 20px;
    width: auto;
    margin-left: 85px;
    &:before {
      @include bg($check-icon);
      margin-bottom: 7px;
    }
  }
  
  .life__open-form {
    margin-right: 125px;
    width: 370px;
    margin-top: 50px;
    padding-top: 17px;
    padding-bottom: 17px;
  }
}
