.request__wrap {
  padding: 20px;
}

.request__ttl {
  @include ttl;  
}

.request__ttl-b {
  color: $color-1;
}

.request__form {
  @include flex;
  align-items: center;
}

.request__input {
  @include input;
}

.request__submit {
  @include btn;
  background: #fff;
  margin-top: 20px;
  border-radius: 6px;
  font-size: 15px;
  font-family: $font-1-l;
}

.request__txt {
  font-family: $font-1-l;
  font-size: 16px;
  text-align: center;
  color: $color-3;
}

.request__txt-b {
  font-family: $font-1;
}

.request__mobile-hide {
  display: none;
}

.form-ty { 
  position: fixed;  
  background: rgba(0,0,0, 0.3);
  height: 100%;  
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;  
  display: none;
  animation: form-cb__form-hide 0.8s;
  justify-content: center;
  align-items: center;
}

.form-ty__wrap {   
  background: #fff;
  height: 161px;  
  border-radius: 1px;  
  width: 280px; 
  animation: form-cb__form-wrap-hide 0.4s;
}

.form-ty__close {
  position: relative;
  @include bg($close-form);
  margin-left: auto;
  margin-right: -10px;
  top: -10px;
  cursor: pointer;
}

.ty__form-wrap {    
  background: #fff; 
  width: 280px;    
  animation: form-cb__form-wrap-hide 0.4s;  
}

.ty__form-wrap-show {
  animation: form-cb__form-wrap-show  0.4s;
}

.ty__form-hide {
  @include flex;
}

.form-ty__open {
  @include flex;  
  animation: form-cb__form-show 1.1s;
}

.form-ty__ttl {
  font-family: $font-1-b;
  font-size: 20px;
  color: $color-3;  
  margin: 0;
  text-align: center;
  margin-top: 20px;
  text-transform: uppercase;
  font-weight: normal;
}

.form-ty__txt {
  font-family: $font-1;
  font-size: 15px;
  text-align: center;
}


@media #{$tablet} {
  .request__wrap {
    max-width: $max-tablet;
    margin: auto;
  }
  
  .request__ttl {
    font-size: $h1-d;
  }
  
  .request__form {
    margin-top: 40px;
  }
  
  .request__input {
    width: 335px;
    margin-top: 40px;
    border-radius: 10px;
  }
  
  .request__submit {
    width: 370px;
    margin-top: 40px;
    font-size: 18px;
    border-radius: 10px;
  }
  
  .request__mobile-hide {
    display: block;
  }
  
  .request__txt {
    font-size: 18px;
    margin-top: 40px;
  }
}


@media #{$desktop} {
  .request__wrap {
    @include flex;
    justify-content: center;
    max-width: $max-desktop;
    margin-top: 30px;
  }
  
  .request__ttl {    
    display: inline-block;
    text-align: center;
  }
  
  .request__ttl-b {
    display: inline-block;
    margin-top: 20px;
  }
  
  .request__form {
    flex-direction: row;
    justify-content: space-between;
    margin-top: 60px;
  }
  
  .request__input {
    margin: 0;
  }
  
  .request__submit {
    margin: 0;
    padding-top: 12px;
    padding-bottom: 12px;    
  }
  
  .form-ty__wrap {
    width: 360px;
  }
}