.we-love__wrap {
  padding: 20px;
  padding-bottom: 40px;
}

.we-love__ttl {
  @include ttl;
}

.we-love__txt {
  font-family: $font-2-l;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}

.we-love__logo {
  display: block;
  margin: auto;
  width: 280px;
}

.we-love__items {
  @include nl;
  @include flex;
  align-items: center;
  margin-top: 30px;
}

.we-love__item {
  @include flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  margin-bottom: 30px;
  margin: auto;
}

.we-love__img {
  @include flex;
  justify-content: center;
  align-items: center;
  width: auto !important;
  margin: auto;
}

.we-love__macbook {
  display: block;
  width: 100%;
}

.we-love__iphone {
  height: 199px;
  width: 106px;
}

.we-love__indicators {
  @include dots;
}

.we-love__indicator {
  @include dot;
}


@media #{$tablet} {
  .we-love__wrap {
    max-width: $max-tablet;
    margin: auto;
    padding-top: 70px;
    padding-bottom: 65px;
  }
  
  .we-love__ttl {
    font-size: $h1-d;    
  }
  
  .we-love__txt {
    font-size: 20px;
    line-height: 30px;
  }
  
  .we-love__logo {
    width: auto;
    margin-top: 30px;
  }
  
  .we-love__items {
    @include flex-row;
    margin-top: 60px;
  }
}


@media #{$desktop} {
  .we-love__wrap {
    max-width: $max-desktop;
  }
  
  .we-love__items {    
    justify-content: center;    
  }
  
  .we-love__item {
    margin: 0;
  }
  
  .we-love__iphone {
    margin-right: 130px;
  }
  
  .we-love__macbook {
    width: auto;
    margin-right: 120px;    
  }
  
  .we-love__beauty {
    margin-right: -120px;
    margin-left: -40px;
  }
}

