.header__wrap {
  padding: 20px;
  background: rgba(0,0,0, 0.1);
}

.header__logo-block {
  @include flex-row;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.header__logo {
  @include bg($spa-logo);
  margin-right: 2px;
}

.header__logo-txt {
  font-family: $font-1-bk-italic;
  font-size: 18px;  
  text-transform: uppercase;
  color: #fff;
}

.header__contacts {
  @include flex;
  align-items: center;  
}

.header__phone {
  font-family: $font-2-b;
  font-size: 22px;
  color: #fff;
  margin-top: 20px;
  &:before {
    content: '';
    display: inline-block;
    @include bg($phone-logo);
    margin-right: 8px;
  }
}

.header__txt {
  font-family: $font-1-l;
  color: #fff;
  font-size: 18px; 
}

/**forms**/

@keyframes form-cb__form-show {
  0% {
    display: none;      
    background: none;
    background: rgba(0,0,0, 0.0);
  }
  50% {    
    display: flex;
    z-index: 100;    
  }
  100% {      
    opacity: 1;
    z-index: 100;
  }
}

@keyframes form-cb__form-hide {
  0% {
    display: flex;    
    z-index: 99;
    opacity: 1;
  }
  30% {    
    opacity: 0;
  }
  99% {
    opacity: 0;    
   
  }
  100% {
    opacity: 0;    
    display: none;
  }
}

@keyframes form-cb__form-wrap-show {
  0% {
    display: none;      
    transform: scale(0.1);    
  }
  20% {    
    
    display: flex;
    z-index: 100;
    opacity: 1;
  }
  100% {        
    z-index: 100;
  }
}

@keyframes form-cb__form-wrap-hide {
  0% {
    display: none; 
  }
  20% {    
    
    display: flex;
    z-index: 100;
    opacity: 1;
  }
  100% {    
    transform: scale(0.1);
    z-index: 100;
  }
}

.form-cb__form {
  @include flex;
  position: fixed;
  background: rgba(0,0,0, 0.3);
  height: 100%;  
  width: 100%;
  top: 0; 
  left: 0;
  z-index: 100;   
  animation: form-cb__form-hide 0.8s;
  justify-content: center;
  align-items: center;
  display: none;
}

.form-cb__form-wrap {    
  background: #fff; 
  width: 280px;    
  animation: form-cb__form-wrap-hide 0.4s;  
}

.form-cb__form-wrap-show {
  animation: form-cb__form-wrap-show  0.4s;
}

.form-cb__close-btn {
  position: relative;
  @include bg($close-form);
  cursor: pointer;
  margin-left: auto;
  margin-right: -10px;  
  margin-top: -10px;
}

.form-cb__wrap {  
  padding: 20px;
  padding-top: 0;
  
}

.form-cb__ttl {
  font-family: $font-1-b;
  color: $color-2;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  font-weight: normal;  
  margin: 0;
}

.form-cb__txt {
  display: block;
  font-size: 15px;
  color: $color-2;
  text-align: center;
}

.form-cb__input {
  border: 1px solid #f8f8f8;
  border-width: 1px;
  border-radius: 7px;
  background: #f8f8f8;
  font-family: $font-1;
  color: $color-2;
  font-size: 16px;
  padding: 11px;  
  padding-left: 40px;  
  width: 190px;
  margin-top: 15px;
  outline: none;
  &:hover {
    background: #fff;
    border: 1px solid $color-1;
  }
  &:focus {
    border-color: $color-1;
    background: #fff;
  }
}

.form-cb__submit {
  @include btn;
  margin-top: 20px;
  background: #fff;
  width: 240px;
  font-size: 16px;
  padding: 10px;
  border-radius: 6px;
  font-family: $font-1;
  border-width: 1px;
}

.form-cb__info {
  margin-top: 20px;
  display: block;
  font-family: $font-2-l;
  font-size: 12px;
  line-height: 20px;
  color: #838383;
  text-align: center;
}

.form-cb__form-hide {
  @include flex;
}

.form-cb__form-open {
  @include flex;   
  animation: form-cb__form-show 1.1s;
}


@media #{$tablet} {
  .header__wrap {
    max-width: $max-tablet;
    @include flex-row;
    justify-content: space-between;
    align-items: center;
    margin: auto;
  }
  
  .header__contacts {
    margin: 0;
  }
  
  .header__phone {
    margin: 0;
  }
}


@media #{$desktop} {
  .header__wrap {
    max-width: $max-desktop;
    padding-left: 30px;
    padding-right: 30px;
  }
  
    .header__cb-nbr {
    font-size: 20px;   
    line-height: 34px;
    letter-spacing: 0.5px;
    margin-top: 5px;
    &:before {
      margin-right: 15px;
    }
  }
  
  .header__cb-btn {
    padding-top: 9px;
    padding-bottom: 9px;
    width: 190px; 
    margin-top: 3px;
  }
  
  .form-cb__wrap {
    width: 360px;
    padding-top: 20px;
  }
  
  .form-cb__form-wrap {
    width: auto;
  }
  
  .form-cb__real-form {
    margin-top: 30px;
  }
  
  .form-cb__input {
    display: block;
    width: 230px;
    margin: auto;
    margin-bottom: 15px;
  }
  
  .form-cb__submit {
    @include btn;
    border-radius: 5px;
    margin-top: 20px;
    border-width: 1px;
    width: 285px;
    font-size: 16px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-family: $font-1;
  }
  
  .form-cb__info {
    font-size: 14px;
  }
}
