.money {
  margin-top: -174px;
  padding-top: 174px;  
  background: url(%=static=%/content/money-bg-mobile.jpg)no-repeat 50%;    
  background-size: cover;
}

.money__wrap {
  margin-top: 10px;
  padding: 20px;  
  padding-top: 0;
}

.money__txt {
  font-family: $font-2-l;
  font-size: 17px;
  color: #fff;
  text-align: center;
  text-shadow: 0px 0px 1px rgba(0,0,0, 0.4);
}

.money__txt-b {
  display: block;
  font-family: $font-2-b;
}

.money__girl {
  margin-top: 10px;
  height: 250px;  
  background: url(%=static=%/content/money-girl.png)no-repeat 50%;
  background-size: contain;
}

.money__open-form {
  @include btn;  
}

.money__items {
  @include nl;  
  align-items: center;
  margin-top: 20px;
  display: none;
}

.money__item {
  width: 280px;
  color: #fff;
  text-align: center;
}


@media #{$tablet} {
  .money {
    background: url(%=static=%/content/money-bg.jpg)no-repeat 40% 0%;  
  }
  
  .money__wrap {
    max-width: $max-tablet;
    padding-bottom: 30px;
    margin: auto;
  }
  
  .money__txt {
    margin-left: auto;
    width: 520px;
    text-align: right;
    font-size: 30px;
    line-height: 45px;
    margin-top: 110px;    
  }
  
  .money__girl {
    display: none;    
  }
  
  .money__open-form {    
    margin-left: auto;
    margin-right: 0;
    font-size: 20px;
    width: 420px;
    margin-top: 50px;
  }
  
  .money__items {
    @include flex;
    flex-direction: row;
    margin-top: 90px;
  }
  
  .money__item {
    text-shadow: 0px 0px 2px rgba(0,0,0, 0.9);
  }
}


@media #{$desktop} {
  .money {
    background-position: 50% 0%;
    margin-top: -104px;
    padding-top: 104px;
  }
  
  .money__wrap {
    max-width: $max-desktop;
    padding-right: 30px;
    padding-left: 30px;
    padding-bottom: 80px;
  }
  
  .money__txt {
    font-size: 35px;
    margin-top: 170px;
    width: 570px;
    line-height: 54px;
  }
  
  .money__open-form {
    margin-top: 60px;
    width: 470px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  
  .money__items {
    justify-content: center;
    margin-top: 120px;
  }
  
  .money__item {
    width: auto;
    margin-right: 15px;
    font-size: 18px;
    line-height: 16px;
  }
  
  .money__item:not(:last-of-type) {
    border-right: 2px solid #fff;
    padding-right: 15px;
  }
}