.why__wrap {  
  padding: 20px;
}

.why__ttl {
  @include ttl;
}

.why__logo {    
  display: block;
  margin: auto; 
  margin-top: 10px;
  width: 280px;
}

.why__items {
  @include nl;
  margin-top: 40px;
}

.why__item {
  @include flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 272px;
}

.why__txt {
  font-family: $font-1-l;
  font-size: 17px;
  margin-top: 25px;
  text-align: center;
  line-height: 25px;
}

.why__img-block {
  @include flex;
  justify-content: center;
  align-items: center;
  height: 105px;  
}

.why__open-form {
  @include btn;
  margin-top: 40px;
}

.why__img-0 {
  @include bg($img1);
}

.why__img-1 {
  @include bg($img2);
}

.why__img-2 {
  @include bg($img3);
}

.why__img-3 {
  @include bg($img4);
}

.why__img-4 {
  @include bg($img5);
}

.why__img-5 {
  @include bg($img6);
}

.why__indicators {  
  @include dots;
}

.why__indicator {
  @include dot;
}


@media #{$tablet} {
  .why__wrap {
    max-width: $max-tablet;
    padding-top: 60px;
    padding-bottom: 50px;
    margin: auto;
  }
  
  .why__ttl {
    font-size: 30px;
  }
  
  .why__logo {
    margin-top: 20px;
    width: auto;
  }
  
  .why__items {
    @include flex-row;
    flex-wrap: wrap;
  }
  
  .why__item {
    margin-bottom: 20px;
    min-height: 180px;
  }
  
  .why__open-form {
    font-size: 16px;
    width: 370px;
  }
}


@media #{$desktop} {
  .why__wrap {
    max-width: $max-desktop;
  }
  
  .why__items {
    margin-top: 60px;
  }
  
  .why__item {
    width: 33.3%;
  }
  
  .why__open-form {
    width: 370px;
  }
}