.map {
  
}

.map__mobile {
  height: 250px;
  width: 100%;
  background: url(%=static=%/content/map.jpg) 68% 55%;
}

.map__wrap {
  
}

.map__info {
  @include flex;
  align-items: center;
  background: rgba(0,0,0, 0.5);
  color: #fff;
  padding-bottom: 10px;
}

.map__items {
  margin: 0;
  @include nl;
  font-size: 18px;
  padding: 20px;
  padding-bottom: 0;
  width: 280px;
}

.map__item {
  font-family: $font-1-l;
  font-size: 16px;
  margin-top: 5px;
}

.map__item-city:before {
  content: '';
  display: inline-block;
  @include bg($mark-icon);
  margin-right: 8px;
}

.map__item-phone:before {
  content: '';
  display: inline-block;
  @include bg($phone-icon);
  margin-right: 6px;
  margin-left: -4px;
}

.map__item-clock:before {
  content: '';
  display: inline-block;
  @include bg($clock-icon);
  margin-right: 6px;
  margin-left: -4px;
}

.map__info-txt {
  font-family: $font-1-l;
  font-size: 16px;
  padding-left: 20px;
  padding-right: 20px;
}


@media #{$tablet} {
  .map {
    background: url(%=static=%/content/map.jpg)no-repeat 60% 55%;
  }
  
  .map__wrap {
    max-width: $max-tablet;
    margin: auto;    
  }
  
  .map__mobile {
    display: none;
  }
  
  .map__info {
    width: 350px;
  }
  
  .map__items {
    padding-top: 45px;
    padding-bottom: 45px;
  }
}


@media #{$desktop} {
  .map {
    background: url(%=static=%/content/map-bg.png)no-repeat 50% 0%;
    margin-bottom: -20px;
    position: relative;
    z-index: 1;
  }
  .map__wrap {
    max-width: $max-desktop;
    margin-top: 50px;
    padding-bottom: 30px;
  }
  
  .map__info {
    background: none;
  }
  
  .map__items {
    padding-bottom: none;
    width: 310px;
    padding-left: 20px;
    padding-right: 20px;
  }
  
  .map__item {
    font-size: 18px;
  }
  
  .map__info-txt {
    font-size: 18px;
    margin-top: -15px;
  }
}